import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useLocation } from 'react-router-dom';
import './App.css';
import TodoList from './pages/TodoList';
import MeetingNotes from './pages/MeetingNotes';
import JsonFormatter from './pages/JsonFormatter';
import JsonCompare from './pages/JsonCompare';
import QrGenerator from './pages/QrGenerator';
import WbsManager from './pages/WbsManager';

function App() {
  return (
    <Router>
      <div className="app">
        <header className="app-header">
          <Link to="/" className="app-logo">
            <h1>Work Util</h1>
          </Link>
          <Navigation />
        </header>
        <main className="app-main">
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/todos" element={<TodoList />} />
            <Route path="/meeting-notes" element={<MeetingNotes />} />
            <Route path="/json-formatter" element={<JsonFormatter />} />
            <Route path="/json-compare" element={<JsonCompare />} />
            <Route path="/qr-generator" element={<QrGenerator />} />
            <Route path="/wbs" element={<WbsManager />} />
          </Routes>
        </main>
      </div>
    </Router>
  );
}

function Navigation() {
  const location = useLocation();
  
  const menuItems = [
    { path: '/todos', label: '', icon: '' },
    { path: '/meeting-notes', label: '', icon: '' },
    { path: '/json-formatter', label: 'JSON', icon: '{}' },
    { path: '/json-compare', label: '', icon: '' },
    { path: '/qr-generator', label: 'QR', icon: '' },
    { path: '/wbs', label: 'WBS', icon: '' }
  ];

  return (
    <nav className="app-nav">
      {menuItems.map((item) => (
        <Link 
          key={item.path}
          to={item.path} 
          className={`nav-item ${location.pathname === item.path ? 'active' : ''}`}
        >
          <span className="nav-icon">{item.icon}</span>
          <span className="nav-label">{item.label}</span>
        </Link>
      ))}
    </nav>
  );
}

function Home() {
  return (
    <div className="home">
      <h2>    </h2>
      <div className="tool-grid">
        <Link to="/todos" className="tool-card">
          <h3> </h3>
          <p>   </p>
        </Link>
        <Link to="/meeting-notes" className="tool-card">
          <h3> </h3>
          <p>    </p>
        </Link>
        <Link to="/json-formatter" className="tool-card">
          <h3>JSON </h3>
          <p>JSON    </p>
        </Link>
        <Link to="/json-compare" className="tool-card">
          <h3>JSON </h3>
          <p> JSON   </p>
        </Link>
        <Link to="/qr-generator" className="tool-card">
          <h3>QR </h3>
          <p> URL QR  </p>
        </Link>
        <Link to="/wbs" className="tool-card">
          <h3>WBS </h3>
          <p>   </p>
        </Link>
      </div>
    </div>
  );
}

export default App;