import React, { useState, useCallback, useRef } from 'react';
import QRCode from 'qrcode';
import '../styles/QrGenerator.css';

interface QROptions {
  errorCorrectionLevel: 'L' | 'M' | 'Q' | 'H';
  type: 'image/png' | 'image/jpeg';
  quality: number;
  margin: number;
  color: {
    dark: string;
    light: string;
  };
  width: number;
}

const QrGenerator: React.FC = () => {
  const [inputText, setInputText] = useState('');
  const [qrCodeUrl, setQrCodeUrl] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [qrOptions, setQrOptions] = useState<QROptions>({
    errorCorrectionLevel: 'M',
    type: 'image/png',
    quality: 0.92,
    margin: 4,
    color: {
      dark: '#000000',
      light: '#FFFFFF'
    },
    width: 256
  });
  const canvasRef = useRef<HTMLCanvasElement>(null);

  const generateQRCode = useCallback(async () => {
    if (!inputText.trim()) {
      setError(' .');
      return;
    }

    setIsLoading(true);
    setError('');

    try {
      const canvas = canvasRef.current;
      if (canvas) {
        await QRCode.toCanvas(canvas, inputText, {
          errorCorrectionLevel: qrOptions.errorCorrectionLevel,
          margin: qrOptions.margin,
          color: qrOptions.color,
          width: qrOptions.width
        });

        const dataUrl = canvas.toDataURL(qrOptions.type, qrOptions.quality);
        setQrCodeUrl(dataUrl);
      }
    } catch (err) {
      setError('QR   .');
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  }, [inputText, qrOptions]);

  const downloadQRCode = () => {
    if (!qrCodeUrl) return;

    const link = document.createElement('a');
    link.download = `qrcode.${qrOptions.type.split('/')[1]}`;
    link.href = qrCodeUrl;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const copyToClipboard = async () => {
    if (!qrCodeUrl) return;

    try {
      const response = await fetch(qrCodeUrl);
      const blob = await response.blob();
      
      await navigator.clipboard.write([
        new ClipboardItem({
          [blob.type]: blob
        })
      ]);
      
      alert('QR   .');
    } catch (err) {
      console.error('  :', err);
      alert('  .');
    }
  };

  const loadPresets = (type: string) => {
    switch (type) {
      case 'url':
        setInputText('https://example.com');
        break;
      case 'email':
        setInputText('mailto:user@example.com');
        break;
      case 'phone':
        setInputText('tel:+82-10-1234-5678');
        break;
      case 'sms':
        setInputText('sms:+82-10-1234-5678?body=');
        break;
      case 'wifi':
        setInputText('WIFI:T:WPA;S:MyNetwork;P:MyPassword;H:false;');
        break;
      case 'vcard':
        setInputText('BEGIN:VCARD\nVERSION:3.0\nFN:\nORG:\nTEL:+82-10-1234-5678\nEMAIL:kim@example.com\nEND:VCARD');
        break;
      default:
        setInputText('');
    }
  };

  const clearAll = () => {
    setInputText('');
    setQrCodeUrl('');
    setError('');
  };

  React.useEffect(() => {
    if (inputText.trim()) {
      const timer = setTimeout(() => {
        generateQRCode();
      }, 300);
      return () => clearTimeout(timer);
    } else {
      setQrCodeUrl('');
      setError('');
    }
  }, [inputText, generateQRCode]);

  return (
    <div className="qr-generator-container">
      <div className="qr-generator-header">
        <h2>QR </h2>
        <button className="clear-btn" onClick={clearAll}> </button>
      </div>

      <div className="qr-content">
        <div className="qr-input-section">
          <div className="input-group">
            <label> </label>
            <textarea
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              placeholder="URL, , ,   ..."
              rows={5}
            />
            <div className="character-count">
              {inputText.length} 
            </div>
          </div>

          <div className="preset-buttons">
            <h3> </h3>
            <div className="preset-grid">
              <button onClick={() => loadPresets('url')}> URL</button>
              <button onClick={() => loadPresets('email')}></button>
              <button onClick={() => loadPresets('phone')}></button>
              <button onClick={() => loadPresets('sms')}>SMS</button>
              <button onClick={() => loadPresets('wifi')}>WiFi</button>
              <button onClick={() => loadPresets('vcard')}> (vCard)</button>
            </div>
          </div>

          <div className="qr-options">
            <h3>QR  </h3>
            
            <div className="option-row">
              <label>  </label>
              <select
                value={qrOptions.errorCorrectionLevel}
                onChange={(e) => setQrOptions({
                  ...qrOptions,
                  errorCorrectionLevel: e.target.value as QROptions['errorCorrectionLevel']
                })}
              >
                <option value="L"> (~7%)</option>
                <option value="M"> (~15%)</option>
                <option value="Q"> (~25%)</option>
                <option value="H"> (~30%)</option>
              </select>
            </div>

            <div className="option-row">
              <label> ()</label>
              <input
                type="range"
                min="128"
                max="512"
                step="32"
                value={qrOptions.width}
                onChange={(e) => setQrOptions({
                  ...qrOptions,
                  width: Number(e.target.value)
                })}
              />
              <span>{qrOptions.width}px</span>
            </div>

            <div className="option-row">
              <label></label>
              <input
                type="range"
                min="0"
                max="10"
                value={qrOptions.margin}
                onChange={(e) => setQrOptions({
                  ...qrOptions,
                  margin: Number(e.target.value)
                })}
              />
              <span>{qrOptions.margin}</span>
            </div>

            <div className="color-options">
              <div className="color-group">
                <label></label>
                <input
                  type="color"
                  value={qrOptions.color.dark}
                  onChange={(e) => setQrOptions({
                    ...qrOptions,
                    color: { ...qrOptions.color, dark: e.target.value }
                  })}
                />
              </div>
              <div className="color-group">
                <label></label>
                <input
                  type="color"
                  value={qrOptions.color.light}
                  onChange={(e) => setQrOptions({
                    ...qrOptions,
                    color: { ...qrOptions.color, light: e.target.value }
                  })}
                />
              </div>
            </div>

            <div className="option-row">
              <label> </label>
              <select
                value={qrOptions.type}
                onChange={(e) => setQrOptions({
                  ...qrOptions,
                  type: e.target.value as QROptions['type']
                })}
              >
                <option value="image/png">PNG</option>
                <option value="image/jpeg">JPEG</option>
              </select>
            </div>

            {qrOptions.type === 'image/jpeg' && (
              <div className="option-row">
                <label></label>
                <input
                  type="range"
                  min="0.1"
                  max="1"
                  step="0.1"
                  value={qrOptions.quality}
                  onChange={(e) => setQrOptions({
                    ...qrOptions,
                    quality: Number(e.target.value)
                  })}
                />
                <span>{Math.round(qrOptions.quality * 100)}%</span>
              </div>
            )}
          </div>
        </div>

        <div className="qr-output-section">
          <div className="qr-preview">
            <h3>QR  </h3>
            
            {error && (
              <div className="error-message">{error}</div>
            )}

            {isLoading && (
              <div className="loading">QR   ...</div>
            )}

            <div className="qr-display">
              <canvas
                ref={canvasRef}
                style={{ display: qrCodeUrl ? 'block' : 'none' }}
              />
              {!qrCodeUrl && !isLoading && !error && (
                <div className="placeholder">
                    QR  .
                </div>
              )}
            </div>

            {qrCodeUrl && (
              <div className="qr-actions">
                <button className="download-btn" onClick={downloadQRCode}>
                  
                </button>
                <button className="copy-btn" onClick={copyToClipboard}>
                   
                </button>
              </div>
            )}
          </div>

          <div className="qr-info">
            <h3>QR  </h3>
            <div className="info-list">
              <div className="info-item">
                <span> :</span>
                <span>{inputText.length} </span>
              </div>
              <div className="info-item">
                <span>:</span>
                <span>{qrOptions.width}  {qrOptions.width}px</span>
              </div>
              <div className="info-item">
                <span> :</span>
                <span>{qrOptions.errorCorrectionLevel} </span>
              </div>
              <div className="info-item">
                <span> :</span>
                <span>{qrOptions.type.split('/')[1].toUpperCase()}</span>
              </div>
              {qrCodeUrl && (
                <div className="info-item">
                  <span> :</span>
                  <span>{Math.round(qrCodeUrl.length * 0.75 / 1024)} KB</span>
                </div>
              )}
            </div>
          </div>

          <div className="usage-tips">
            <h3> </h3>
            <ul>
              <li>URL http://  https://  </li>
              <li>WiFi: WIFI:T:WPA;S:;P:;H:false;</li>
              <li>: tel:+82-10-1234-5678</li>
              <li>: mailto:user@example.com</li>
              <li>SMS: sms:+82-10-1234-5678?body=</li>
              <li>    QR      </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default QrGenerator;